import { getDatabase, ref, onValue } from "firebase/database"

export default{
    state:{
        info: {}
    },

    actions:{
        async fetchInfo({dispatch, commit}){
            const uid = await dispatch('getUid')
            return await onValue(ref(getDatabase(), `users/${uid}/info`), (snapshot) => {
                const info = snapshot.val()
                commit('setInfo', info)
            },
            {
                onlyOnce: true
            })
        }
    },
    mutations:{
        setInfo(state, info){
            state.info = info
        },
        clearInfo(state){
            state.info = {}
        }
    },
    getters:{
        info: s => s.info
    }
}