<template>
  <nav class="navbar orange lighten-1">
    <div class="nav-wrapper">
      <div class="navbar-left">
        <a href="#" @click.prevent="$emit('click')">
          <i class="material-icons black-text">dehaze</i>
        </a>
        <span class="black-text">{{ formatDate(date, "datetime") }}</span>
      </div>

      <ul class="right hide-on-small-and-down">
        <li>
          <a
            class="dropdown-trigger black-text"
            href="#"
            data-target="dropdown"
            ref="dropdown"
          >
            {{ name }}
            <i class="material-icons right">arrow_drop_down</i>
          </a>

          <ul id="dropdown" class="dropdown-content">
            <li>
              <router-link to="/profile" class="black-text">
                <i class="material-icons">account_circle</i>Профиль
              </router-link>
            </li>
            <li class="divider" tabindex="-1"></li>
            <li>
              <a class="black-text" @click="logout">
                <i class="material-icons">assignment_return</i>Выйти
              </a>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
export default {
  emits: ["click"],
  data() {
    return {
      date: new Date(),
      inerval: null,
      dropdown: null,
    };
  },
  mounted() {
    // console.log(this.$refs.dropdown);
    (this.dropdown = M.Dropdown.init(this.$refs.dropdown, {
      constrainWidth: false,
    })),
      (this.interval = setInterval(() => {
        (this.date = new Date()), 1000;
      }));
  },

  computed: {
    name() {
      return this.$store.getters.info.name;
    },
  },

  beforeUnmount() {
    console.log("befere unmount");
    clearInterval(this.interval);
    if (this.dropdown && this.dropdown.destroy) {
      this.dropdown.destroy();
    }
  },

  methods: {
    async logout() {
      await this.$store.dispatch("logout");
      this.$router.push("/login?message=logout");
    },

    formatDate(dateStr, showSelect) {
      const date = new Date(dateStr);
      const options = {};

      if (showSelect.includes("date")) {
        options.day = "numeric";
        options.month = "long";
        options.year = "numeric";
      }

      if (showSelect.includes("time")) {
        options.hour = "2-digit";
        options.minute = "2-digit";
        options.second = "2-digit";
      }

      return new Intl.DateTimeFormat("ru-RU", options).format(this.date);
    },
  },
};
</script>