import { createApp } from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router/router'
import store from './store/store'
import 'materialize-css/dist/js/materialize.min'
import loader from './components/app/Loader.vue'

import { initializeApp } from 'firebase/app'
import { getAuth } from 'firebase/auth'
import 'firebase/database'

const fireAppConfig = initializeApp({
    apiKey: "AIzaSyAZOlQ4nIhnTLSpctA7QcFcFX0vIpZnQOQ",
    authDomain: "vue-crm-2-5d241.firebaseapp.com",
    projectId: "vue-crm-2-5d241",
    storageBucket: "vue-crm-2-5d241.appspot.com",
    messagingSenderId: "1054173414540",
    appId: "1:1054173414540:web:3de09b275a14a704ba67a9",
    measurementId: "G-ET27EEYKCJ"
})

let app;

getAuth().onAuthStateChanged(() => {
    if (!app) {
        app = createApp(App).use(store).use(router).use(loader).mount('#app')
    }
})



