<template>
  <div class="grey darken-1 empty-layout">
    <router-view />
  </div>
</template>

<script>

export default {
  computed: {
    error() {
      return this.$store.getters.error;
    },
  },

  watch:{
    error(fbError){
      if(fbError.message.includes('auth/user-not-found')){
        M.toast({ html: "[Ошибка]: Пользователя с таким email не существует" });
      }
      if(fbError.message.includes('auth/wrong-password')){
        M.toast({html: '[Ошибка]: Неверный пароль'})
      }
      if(fbError.message.includes('auth/email-already-in-use')){
        M.toast({html: '[Ошибка]: Email уже занят'})
      }
    }
  }
};
</script>