import { createStore } from 'vuex'
import auth from './auth'
import info from './info'

export default createStore({
  state: {
    error: null
  },

  mutations: {
    setError(state, error) {
      state.error = error
    },

    clearError(state) {
      state.error = null
    }
  },

  getters: {
    error: s => s.error
  },

  actions: {
    async fetchCurrency() {
      const key = process.env.VUE_APP_FIXER

      const options = {method: 'GET', headers: {Accept: 'application/json'}};

      const res = await fetch(`https://api.fastforex.io/fetch-multi?from=USD&to=RUB%2C%20USD%2C%20UZS&api_key=${key}`, options)
      return await res.json()

    }
  },

  modules: {
    auth,
    info,
  }
})
